import cuCustom from '@/colorui/components/cu-custom.vue'
export default {
	data() {
		return {
			siteAll: []
		}
	},
	methods: {
		siteAllShow() {
			// const that = this
			let id = uni.getStorageSync('user_id')
			// console.log(id)
			// 获取数据
			// console.log(1)
			uni.request({
				// 接口
				url: 'http://47.104.29.236:9999/api/v1/siteAll',
				// 请求方式
				method: 'GET',
				// 携带的数据
				data: {id:id},
				// 请求成功
				success: res => {
					// console.log(res)
					this.siteAll = res.data.data
					console.log(this.siteAll)
					for(var i = 0;i<this.siteAll.length;i++) {
						if(this.siteAll[i].sdefault == 1) {
							let aa = this.siteAll[i]
							this.siteAll.splice(i,1);
							this.siteAll.unshift(aa)
						}
					}
					// console.log(this.siteAll)
					
				},
				// 请求失败
				fail: () => {},
				complete: () => {}
			})
			console.log(this.siteAll)
		},
		alterSite(id) {
			console.log(1)
			console.log(id)
			uni.navigateTo({
				url:'../xy-alertSite/xy-alertSite/xy-alertSite',
				success:res =>{},
				fail:()=>{},
				complete:()=>{}
			})
			uni.setStorage({
			    key: 'id',
			    data: id,
			    success: function () {
			        // console.log('success');
			    }
			});
		},
		addSite() {
			uni.navigateTo({
				url:'../xy-addSite/addSite/addSite',
				success:res =>{},
				fail:()=>{},
				complete:()=>{}
			})
		},
	    // 更换地址
		change(e){
	    	// 点击地址的id
	    	let id = e.currentTarget.dataset.id
	    	uni.navigateTo({
	    		url:`../xy-alertSite/xy-alertSite/xy-alertSite?id=${id}&path=${this.previousPage.path}`,
	    		success:res =>{},
	    		fail:()=>{},
	    		complete:()=>{}
	    	})
	    }
	},
	components: {
		cuCustom
	},
	created() {
		this.siteAllShow()
	},
	onLoad(e) {
		this.siteAllShow()
		this.previousPage = e
	}
}
